import { useState, useEffect, useRef } from 'react'
import { supabase } from '@/lib/supabase'
import { useGameStore } from '@/store/gameStore'

export function useGeo() {
  const [position, setPosition] = useState<[number, number] | null>(null)
  const [error, setError] = useState<string | null>(null)
  const [isTracking, setIsTracking] = useState(false)
  const watchIdRef = useRef<number | null>(null)
  const positionRef = useRef<[number, number] | null>(null)
  const { updatePlayerPosition } = useGameStore()

  // Funo para atualizar posio manualmente (para controles de teclado)
  const updatePositionManual = (newPosition: [number, number]) => {
    // Atualizao sncrona para melhor responsividade
    setPosition(newPosition)
    positionRef.current = newPosition
    
    // Atualizar gameStore de forma assncrona para no bloquear
    setTimeout(() => {
      updatePlayerPosition(newPosition)
    }, 0)
  }

  useEffect(() => {
    // Verificar se estamos no modo mock
    const gameMode = localStorage.getItem('gameMode')
    const isMockMode = gameMode === 'mock'
    
    // Forar modo mock para teste
    if (isMockMode || true) {
      const initialPosition: [number, number] = [41.1333, -8.6167] // Gaia
      setPosition(initialPosition)
      positionRef.current = initialPosition
      setError(null)
      setIsTracking(true)
      // Atualizar posio no gameStore
      updatePlayerPosition(initialPosition)
    }

    // Verificar se o Supabase est configurado
    if (!supabase) {
      setError('Supabase no configurado')
      return
    }
    
    if (!navigator.geolocation) {
      setError('Geolocalizao no disponvel neste dispositivo')
      return
    }

    setIsTracking(true)

    // Obter posio inicial
    navigator.geolocation.getCurrentPosition(
      (pos) => {
        const newPosition: [number, number] = [pos.coords.latitude, pos.coords.longitude]
        setPosition(newPosition)
        setError(null)
        
                 // Atualizar apenas o gameStore para evitar conflitos
         updatePlayerPosition(newPosition)
      },
      (err) => {
        console.error('useGeo: Erro ao obter posio inicial:', err)
        setError(`Erro GPS: ${err.message}`)
        setIsTracking(false)
      },
      {
        enableHighAccuracy: true,
        timeout: 5000,
        maximumAge: 0
      }
    )

    // Iniciar rastreamento contnuo
    watchIdRef.current = navigator.geolocation.watchPosition(
      (pos) => {
        const newPosition: [number, number] = [pos.coords.latitude, pos.coords.longitude]
        setPosition(newPosition)
        setError(null)
        
                 // Atualizar apenas o gameStore para evitar conflitos
         updatePlayerPosition(newPosition)
      },
      (err) => {
        console.error('useGeo: Erro no rastreamento:', err)
        setError(`Erro GPS: ${err.message}`)
      },
      {
        enableHighAccuracy: true,
        timeout: 5000,
        maximumAge: 0
      }
    )

    return () => {
      if (watchIdRef.current) {
        navigator.geolocation.clearWatch(watchIdRef.current)
        watchIdRef.current = null
      }
    }
  }, [])

  // Monitorar mudanas na posio
  useEffect(() => {
    if (position) {
      positionRef.current = position
    }
  }, [position])

    // Removida funo updatePositionInSupabase para evitar conflitos

  const startTracking = () => {

    if (!navigator.geolocation) {
      setError('Geolocalizao no disponvel')
      return
    }

    setIsTracking(true)

    watchIdRef.current = navigator.geolocation.watchPosition(
      (pos) => {
        const newPosition: [number, number] = [pos.coords.latitude, pos.coords.longitude]
        setPosition(newPosition)
        setError(null)
                 updatePlayerPosition(newPosition)
      },
      (err) => {
        console.error('useGeo: Erro no rastreamento manual:', err)
        setError(`Erro GPS: ${err.message}`)
      },
      {
        enableHighAccuracy: true,
        timeout: 5000,
        maximumAge: 0
      }
    )
  }

  const stopTracking = () => {
    if (watchIdRef.current) {
      navigator.geolocation.clearWatch(watchIdRef.current)
      watchIdRef.current = null
    }
    setIsTracking(false)
  }



  return {
    position,
    error,
    isTracking,
    startTracking,
    stopTracking,
    updatePosition: updatePositionManual
  }
}
