import { useEffect, useState } from 'react'
import { supabase } from '@/lib/supabase'
import { useAuth } from './useAuth'

interface Territory {
  id: string
  team_id: string
  player_id: string
  polygon: {
    type: string
    coordinates: number[][][]
  }
  area: number
  conquered_at: string
  created_at: string
}

interface User {
  id: string
  name: string
  team: 'green' | 'blue' | 'red'
  current_position: [number, number] | null
  score: number
}

const normalizeTeam = (team?: string): 'green' | 'blue' | 'red' => {
  const t = (team || '').toString().toLowerCase().trim()
  if (t === 'red') return 'red'
  if (t === 'blue') return 'blue'
  if (t === 'green') return 'green'
  console.warn('normalizeTeam: Equipe invlida:', team, 'usando fallback green')
  return 'green'
}

export function useRealtime() {
  const { user } = useAuth()
  const [territories, setTerritories] = useState<Territory[]>([])
  const [onlineUsers, setOnlineUsers] = useState<User[]>([])

  useEffect(() => {
    // Verificar se o Supabase est configurado
    const supabaseUrl = import.meta.env.VITE_SUPABASE_URL
    const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY
    
    if (!supabaseUrl || !supabaseAnonKey) {
      console.warn('useRealtime: Supabase no configurado, retornando dados vazios')
      return
    }

    if (!user) return

    // Buscar dados iniciais imediatamente
    fetchTerritories()
    fetchOnlineUsers()
    
    // Forar busca inicial aps um pequeno delay para garantir que o usurio foi criado
    setTimeout(() => {
      fetchOnlineUsers()
      fetchTerritories()
    }, 1000)

    // Verificar se estamos no modo mock
    const gameMode = localStorage.getItem('gameMode')
    const isMockMode = gameMode === 'mock'
    
    if (isMockMode) {
      // No modo mock, usar polling em vez de realtime
      const interval = setInterval(() => {
        fetchTerritories()
        fetchOnlineUsers()
      }, 2000) // Atualizar a cada 2 segundos
      
      return () => clearInterval(interval)
    }

    // Escutar mudanas em territrios
    const territoriesSubscription = supabase
      .channel('territories')
      .on('postgres_changes', 
        { event: '*', schema: 'public', table: 'territories' },
        () => {
          fetchTerritories()
        }
      )
      .subscribe()

    // Escutar mudanas em usurios (posies)
    const usersSubscription = supabase
      .channel('users')
      .on('postgres_changes', 
        { event: '*', schema: 'public', table: 'users' },
        () => {
          fetchOnlineUsers()
        }
      )
      .subscribe()

    return () => {
      territoriesSubscription.unsubscribe()
      usersSubscription.unsubscribe()
    }
  }, [user])

  const fetchTerritories = async () => {
    try {
      const { data, error } = await supabase
        .from('territories')
        .select('*')
        .order('created_at', { ascending: false })

      if (error) throw error
      setTerritories(data || [])
    } catch (error) {
      console.error('Erro ao buscar territrios:', error)
    }
  }

  const fetchOnlineUsers = async () => {
    try {
      const { data, error } = await supabase
        .from('users')
        .select('*')

      if (error) throw error
      
      // Converter posies do formato PostgreSQL para array e normalizar equipa
      const usersWithPositions = (data || []).map(u => ({
        ...u,
        team: normalizeTeam(u.team),
        current_position: u.current_position ? 
          u.current_position.replace(/[()]/g, '').split(',').map(Number) as [number, number] :
          null
      })) as unknown as User[]
      
      setOnlineUsers(usersWithPositions)
    } catch (error) {
      console.error('Erro ao buscar usurios online:', error)
    }
  }

  const addTerritory = async (polygon: any, area: number, team?: string) => {

    if (!user) return

    try {
      // Usar a equipa fornecida ou buscar do usurio
      
      // Buscar dados atualizados do usurio na tabela users
      const { data: userData, error: userError } = await supabase
        .from('users')
        .select('team')
        .eq('id', user.id)
        .single()
      
      if (userError) {
        console.error('addTerritory: Erro ao buscar dados do usurio:', userError)
      }
      
      const teamId = normalizeTeam(team || userData?.team || (user as any).team)
      
      const { data, error } = await supabase
        .from('territories')
        .insert({
          team_id: teamId,
          player_id: user.id,
          polygon,
          area,
          conquered_at: new Date().toISOString()
        })
        .select()
        .single()

      if (error) throw error

      // Buscar score atual do usurio e incrementar
      const { data: me, error: meErr } = await supabase
        .from('users')
        .select('score')
        .eq('id', user.id)
        .single()
      if (meErr) throw meErr
      const newScore = (me?.score || 0) + area
      const { error: scoreError } = await supabase
        .from('users')
        .update({ score: newScore })
        .eq('id', user.id)
      if (scoreError) throw scoreError

      // Refresh imediato para refletir em todos os clientes
      await Promise.all([fetchTerritories(), fetchOnlineUsers()])

      return data
    } catch (error) {
      console.error('Erro ao adicionar territrio:', error)
      throw error
    }
  }

  return {
    territories,
    onlineUsers,
    addTerritory,
    fetchTerritories,
    fetchOnlineUsers
  }
}
