import { createClient } from '@supabase/supabase-js'

const supabaseUrl = import.meta.env.VITE_SUPABASE_URL
const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY

console.log('Supabase URL:', supabaseUrl)
console.log('Supabase Anon Key:', supabaseAnonKey ? 'Presente' : 'Ausente')

if (!supabaseUrl || !supabaseAnonKey) {
  console.error('Variveis de ambiente do Supabase no encontradas!')
  console.error('Crie um arquivo .env.local com:')
  console.error('VITE_SUPABASE_URL=sua_url_do_supabase')
  console.error('VITE_SUPABASE_ANON_KEY=sua_chave_anonima_do_supabase')
  throw new Error('Missing Supabase environment variables')
}

export const supabase = createClient(supabaseUrl, supabaseAnonKey)

// Tipos para o banco de dados
export interface User {
  id: string
  email: string
  name: string
  team: 'green' | 'blue' | 'red'
  score: number
  current_position: [number, number] | null
  created_at: string
}

export interface Team {
  id: string
  name: string
  color: 'green' | 'blue' | 'red'
  score_total: number
  created_at: string
}

export interface Territory {
  id: string
  team_id: string
  player_id: string
  polygon: any // GeoJSON
  area: number
  conquered_at: string
  created_at: string
}

export interface Quiz {
  id: string
  question: string
  correct_answer: string
  options: string[]
  team_id: string
  player_id: string
  points: number
  answered: boolean
  created_at: string
}
