import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom'
import { useAuth } from '@/hooks/useAuth'
import { useTrace } from '@/hooks/useTrace'
import { Login } from '@/pages/Login'
import { MapView } from '@/components/MapView'
import { Controls } from '@/components/Controls'
import { Leaderboard } from '@/pages/Leaderboard'
import { Hud } from '@/components/Hud'

import './styles/index.css'
import { useEffect } from 'react'

function GameLayout() {
  const { user } = useAuth()
  
  // Adicionar sistema de traos
  useTrace()
  
  // Ativar modo mock para controles funcionarem
  useEffect(() => {
    if (!localStorage.getItem('gameMode')) {
      localStorage.setItem('gameMode', 'mock')
    }
  }, [])

  if (!user) {
    return <Navigate to="/login" replace />
  }

  return (
    <div className="game-layout">
      <div className="map-container">
        <MapView />
      </div>
      <div className="ui-overlay">
        <div className="top-left">
          <Controls />
        </div>
        <div className="top-right">
          <Hud />
        </div>
        <div className="bottom-center">
          <div className="navigation">
            <a href="/leaderboard" className="nav-button">
               Leaderboard
            </a>
          </div>
        </div>
      </div>
    </div>
  )
}

function App() {
  // Verificar se as variveis de ambiente esto configuradas
  const supabaseUrl = import.meta.env.VITE_SUPABASE_URL
  const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY
  if (!supabaseUrl || !supabaseAnonKey) {
    return (
      <div className="min-h-screen bg-gray-900 flex items-center justify-center">
        <div className="text-white text-center max-w-md mx-auto p-6">
          <h1 className="text-2xl font-bold mb-4"> Configurao Necessria</h1>
          <p className="mb-4">As variveis de ambiente do Supabase no esto configuradas.</p>
          <div className="bg-gray-800 p-4 rounded mb-4 text-sm">
            <p className="mb-2">Crie um arquivo <code className="bg-gray-700 px-2 py-1 rounded">.env.local</code> na raiz do projeto com:</p>
            <pre className="text-green-400">
{`VITE_SUPABASE_URL=sua_url_do_supabase
VITE_SUPABASE_ANON_KEY=sua_chave_anonima_do_supabase`}
            </pre>
          </div>
          <p className="text-sm text-gray-400">
            Consulte o README.md para instrues detalhadas de configurao.
          </p>
        </div>
      </div>
    )
  }

  const { user, loading } = useAuth()

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-900 flex items-center justify-center">
        <div className="text-white text-xl">Carregando...</div>
      </div>
    )
  }

  return (
    <Router>
      <div className="App">
        <Routes>
          <Route path="/login" element={user ? <Navigate to="/" replace /> : <Login />} />
          <Route path="/leaderboard" element={<Leaderboard />} />
          <Route path="/" element={<GameLayout />} />
        </Routes>
      </div>
    </Router>
  )
}

export default App


