import { useState, useEffect, useMemo } from 'react'
import { useRealtime } from './useRealtime'

interface TerritoryStats {
  conquered: number
  lost: number
  net: number
}

interface TeamStats {
  green: TerritoryStats
  blue: TerritoryStats
  red: TerritoryStats
}

// Funo para detectar sobreposies entre territrios
function detectTerritoryOverlaps(territories: any[]) {
  const overlaps: Array<{
    conqueredTerritory: any
    losingTerritory: any
    overlapArea: number
  }> = []

  for (let i = 0; i < territories.length; i++) {
    for (let j = i + 1; j < territories.length; j++) {
      const t1 = territories[i]
      const t2 = territories[j]
      
      // S verificar entre equipes diferentes
      if (t1.team_id === t2.team_id) continue
      
      // Verificar se h sobreposio temporal e geogrfica
      const t1Date = new Date(t1.conquered_at).getTime()
      const t2Date = new Date(t2.conquered_at).getTime()
      
      // Determinar qual foi conquistado depois (conquistador)
      const conqueringTerritory = t1Date > t2Date ? t1 : t2
      const losingTerritory = t1Date > t2Date ? t2 : t1
      
      // Verificar se esto prximos geograficamente (simplificado)
      const coords1 = t1.polygon?.coordinates?.[0] || []
      const coords2 = t2.polygon?.coordinates?.[0] || []
      
      if (coords1.length === 0 || coords2.length === 0) continue
      
      // Calcular centro dos polgonos
      const center1 = coords1.reduce((acc: number[], coord: number[]) => {
        return [acc[0] + coord[0], acc[1] + coord[1]]
      }, [0, 0]).map((val: number) => val / coords1.length)
      
      const center2 = coords2.reduce((acc: number[], coord: number[]) => {
        return [acc[0] + coord[0], acc[1] + coord[1]]
      }, [0, 0]).map((val: number) => val / coords2.length)
      
      const distance = Math.sqrt(
        Math.pow(center1[0] - center2[0], 2) + 
        Math.pow(center1[1] - center2[1], 2)
      )
      
      // Se esto prximos, considerar sobreposio
      if (distance < 0.002) {
        const overlapArea = Math.min(t1.area, t2.area) * 0.3 // Estimativa
        overlaps.push({
          conqueredTerritory: conqueringTerritory,
          losingTerritory: losingTerritory,
          overlapArea
        })
      }
    }
  }
  
  return overlaps
}

export function useTerritoryStats() {
  const { territories } = useRealtime()
  const [stats, setStats] = useState<TeamStats>({
    green: { conquered: 0, lost: 0, net: 0 },
    blue: { conquered: 0, lost: 0, net: 0 },
    red: { conquered: 0, lost: 0, net: 0 }
  })

  // Calcular estatsticas baseadas nos territrios
  const calculateStats = useMemo(() => {
    const newStats: TeamStats = {
      green: { conquered: 0, lost: 0, net: 0 },
      blue: { conquered: 0, lost: 0, net: 0 },
      red: { conquered: 0, lost: 0, net: 0 }
    }

    // Agrupar territrios por equipe
    const territoriesByTeam = territories.reduce((acc, territory) => {
      const team = territory.team_id as 'green' | 'blue' | 'red'
      if (!acc[team]) acc[team] = []
      acc[team].push(territory)
      return acc
    }, {} as Record<'green' | 'blue' | 'red', typeof territories>)

    // Calcular rea total conquistada por cada equipe
    Object.entries(territoriesByTeam).forEach(([team, teamTerritories]) => {
      const totalArea = teamTerritories.reduce((sum, t) => sum + (t.area || 0), 0)
      newStats[team as keyof TeamStats].conquered = totalArea
    })

    // Detectar sobreposies e calcular territrio perdido
    const overlaps = detectTerritoryOverlaps(territories)
    
    // Calcular territrio perdido baseado em sobreposies reais
    Object.entries(territoriesByTeam).forEach(([team]) => {
      const teamKey = team as keyof TeamStats
      let lostArea = 0
      
      // Somar todas as sobreposies onde esta equipe perdeu
      overlaps.forEach(overlap => {
        if (overlap.losingTerritory.team_id === team) {
          lostArea += overlap.overlapArea
        }
      })
      
      newStats[teamKey].lost = lostArea
      newStats[teamKey].net = newStats[teamKey].conquered - lostArea
    })

    return newStats
  }, [territories])

  // Atualizar estatsticas quando os territrios mudarem
  useEffect(() => {
    setStats(calculateStats)
  }, [calculateStats])

  return {
    stats,
    getTeamStats: (team: 'green' | 'blue' | 'red') => stats[team],
    getTotalConquered: () => Object.values(stats).reduce((sum, team) => sum + team.conquered, 0),
    getTotalLost: () => Object.values(stats).reduce((sum, team) => sum + team.lost, 0)
  }
}
