import { useState, useEffect } from 'react'
import { supabase } from '@/lib/supabase'
import type { User } from '@supabase/supabase-js'

export function useAuth() {
  const [user, setUser] = useState<User | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    console.log('useAuth: Iniciando...')

    // Verificar se o Supabase est configurado
    if (!supabase) {
      console.log('useAuth: Supabase no configurado, retornando null')
      setUser(null)
      setLoading(false)
      return
    }

    console.log('useAuth: Verificando sesso...')
    
    // Obter sesso atual
    supabase.auth.getSession().then(({ data: { session } }) => {
      console.log('useAuth: Sesso atual:', session ? 'Sim' : 'No')
      setUser(session?.user ?? null)
      setLoading(false)
    })

    // Escutar mudanas de autenticao
    const { data: { subscription } } = supabase.auth.onAuthStateChange(
      async (event, session) => {
        console.log('useAuth: Evento de auth:', event)
        setUser(session?.user ?? null)
        setLoading(false)
      }
    )

    return () => subscription.unsubscribe()
  }, [])

  const signIn = async (email: string, password: string) => {
    if (!supabase) {
      throw new Error('Supabase no configurado')
    }

    try {
      console.log('useAuth: Tentando login...')
      const { data, error } = await supabase.auth.signInWithPassword({
        email,
        password
      })
      
      if (error) {
        console.error('useAuth: Erro no login:', error)
        throw error
      }
      
      console.log('useAuth: Login realizado com sucesso')
      
      // Se o login foi bem-sucedido, verificar se o usurio existe na tabela users
      if (data.user) {
        try {
          // Verificar se o usurio j existe na tabela users
          const { error: selectError } = await supabase
            .from('users')
            .select('id')
            .eq('id', data.user.id)
            .single()
          
                     if (selectError && selectError.code === 'PGRST116') {
             // Usurio no existe na tabela users, vamos cri-lo
             console.log('useAuth: Criando usurio na tabela users...')
             console.log('useAuth: Dados do usurio:', {
               id: data.user.id,
               name: data.user.user_metadata?.name,
               team: data.user.user_metadata?.team,
               email: data.user.email
             })
             
             const { error: insertError } = await supabase
               .from('users')
               .insert([
                 {
                   id: data.user.id,
                   name: data.user.user_metadata?.name || 'Jogador',
                   team: data.user.user_metadata?.team || 'green',
                   email: data.user.email || '',
                   score: 0
                 }
               ])
             
             if (insertError) {
               console.error('useAuth: Erro ao criar usurio na tabela:', insertError)
             } else {
               console.log('useAuth: Usurio criado na tabela com sucesso')
             }
           } else {
             console.log('useAuth: Usurio j existe na tabela users')
           }
        } catch (dbError) {
          console.error('useAuth: Erro ao verificar/criar usurio na tabela:', dbError)
        }
      }
      
      return { data, error: null }
    } catch (error) {
      console.error('useAuth: Erro no login:', error)
      return { data: null, error }
    }
  }

  const signUp = async (email: string, password: string, name: string, team: string) => {
    if (!supabase) {
      throw new Error('Supabase no configurado')
    }

    try {
      console.log('useAuth: Tentando registro...')
      const { data, error } = await supabase.auth.signUp({
        email,
        password,
        options: {
          data: {
            name,
            team
          }
        }
      })
      
      if (error) {
        console.error('useAuth: Erro no registro:', error)
        throw error
      }
      
      console.log('useAuth: Registro realizado com sucesso')
      
      // Verificar se o email precisa ser confirmado
      if (data.user && !data.user.email_confirmed_at) {
        console.log('useAuth: Email precisa ser confirmado')
        return { 
          data, 
          error: null, 
          message: 'Conta criada! Verifique seu email para confirmar o endereo antes de fazer login.' 
        }
      }
      
      // Se o email j est confirmado, inserir na tabela users
      if (data.user && data.user.email_confirmed_at) {
        try {
          const { error: insertError } = await supabase
            .from('users')
            .insert([
              {
                id: data.user.id,
                name,
                team,
                email,
                score: 0
              }
            ])
          
          if (insertError) {
            console.error('useAuth: Erro ao inserir usurio:', insertError)
            // No vamos falhar o registro por causa disso, mas logamos o erro
          }
        } catch (insertError) {
          console.error('useAuth: Erro ao inserir usurio:', insertError)
        }
      }
      
      return { data, error: null }
    } catch (error) {
      console.error('useAuth: Erro no registro:', error)
      return { data: null, error }
    }
  }

  const signOut = async () => {
    if (!supabase) {
      console.log('useAuth: Supabase no configurado')
      return
    }

    try {
      console.log('useAuth: Fazendo logout...')
      
      // Limpar posio atual antes de fazer logout
      const { data: { user } } = await supabase.auth.getUser()
      if (user) {
        await supabase
          .from('users')
          .update({ current_position: null })
          .eq('id', user.id)
      }
      
      const { error } = await supabase.auth.signOut()
      
      if (error) {
        console.error('useAuth: Erro ao fazer logout:', error)
        throw error
      }
      
      console.log('useAuth: Logout realizado com sucesso')
      setUser(null)
    } catch (error) {
      console.error('useAuth: Erro ao fazer logout:', error)
      throw error
    }
  }

  return { user, loading, signIn, signUp, signOut }
}
