import { create } from 'zustand'
import { supabase } from '@/lib/supabase'

interface Territory {
  id: string
  color: 'green' | 'blue' | 'red'
  points: [number, number][]
  area: number
}

interface Player {
  id: string
  name: string
  team: 'green' | 'blue' | 'red'
  position: [number, number] | null
  score: number
}

interface GameState {
  territories: Territory[]
  players: Player[]
  currentPlayer: Player | null
  loading: boolean
  error: string | null
  
  // Actions
  fetchTerritories: () => Promise<void>
  fetchPlayers: () => Promise<void>
  updatePlayerPosition: (position: [number, number]) => Promise<void>
  captureTerritory: (territoryId: string) => Promise<void>
  resetGame: () => void
}

const normalizeTeam = (team?: string): 'green' | 'blue' | 'red' => {
  const t = (team || '').toString().toLowerCase().trim()
  if (t === 'red') return 'red'
  if (t === 'blue') return 'blue'
  if (t === 'green') return 'green'
  console.warn('normalizeTeam: Equipe invlida:', team, 'usando fallback green')
  return 'green'
}

export const useGameStore = create<GameState>((set, get) => ({
  territories: [],
  players: [],
  currentPlayer: null,
  loading: false,
  error: null,

  fetchTerritories: async () => {

    // Verificar se o Supabase est configurado
    if (!supabase) {
      console.log('gameStore: Supabase no configurado')
      set({ error: 'Supabase no configurado', loading: false })
      return
    }

    set({ loading: true, error: null })

    try {
      console.log('gameStore: Buscando territrios...')
      const { data, error } = await supabase
        .from('territories')
        .select('*')

      if (error) {
        console.error('gameStore: Erro ao buscar territrios:', error)
        set({ error: error.message, loading: false })
        return
      }

      console.log('gameStore: Territrios encontrados:', data?.length || 0)
      set({ territories: data || [], loading: false })
    } catch (error) {
      console.error('gameStore: Erro ao buscar territrios:', error)
      set({ error: 'Erro ao carregar territrios', loading: false })
    }
  },

  fetchPlayers: async () => {

    // Verificar se o Supabase est configurado
    if (!supabase) {
      console.log('gameStore: Supabase no configurado')
      set({ error: 'Supabase no configurado', loading: false })
      return
    }

    set({ loading: true, error: null })

    try {
      console.log('gameStore: Buscando jogadores...')
      const { data, error } = await supabase
        .from('users')
        .select('*')

      if (error) {
        console.error('gameStore: Erro ao buscar jogadores:', error)
        set({ error: error.message, loading: false })
        return
      }

      const players: Player[] = (data || []).map(user => ({
        id: user.id,
        name: user.name,
        team: normalizeTeam(user.team),
        position: user.current_position ? 
          user.current_position.replace(/[()]/g, '').split(',').map(Number) as [number, number] : 
          null,
        score: user.score
      }))

      console.log('gameStore: Jogadores encontrados:', players.length)
      set({ players, loading: false })
    } catch (error) {
      console.error('gameStore: Erro ao buscar jogadores:', error)
      set({ error: 'Erro ao carregar jogadores', loading: false })
    }
  },

  updatePlayerPosition: async (position: [number, number]) => {
    // Verificar se estamos no modo mock
    const gameMode = localStorage.getItem('gameMode')
    const isMockMode = gameMode === 'mock'
    
    // Sempre tentar atualizar o currentPlayer com dados frescos do banco
    if (supabase) {
      try {
        const { data: { user } } = await supabase.auth.getUser()
        if (user) {
          // Buscar dados do usurio na tabela users
          const { data: userData, error: userError } = await supabase
            .from('users')
            .select('team, name')
            .eq('id', user.id)
            .single()
          
          if (userError) {
            console.log('gameStore: Erro ao buscar dados do usurio:', userError)
          } else if (userData) {
            const currentTeam = normalizeTeam(userData.team)
            
            set(() => ({
              currentPlayer: {
                id: user.id,
                name: userData.name || 'Jogador',
                team: currentTeam,
                position: position,
                score: 0
              }
            }))
          }
        }
      } catch (error) {
        console.log('gameStore: Erro ao buscar dados do usurio:', error)
      }
    }

    if (isMockMode) {
      
      // Se no h currentPlayer, criar um mock
      if (!get().currentPlayer) {
        
        // Tentar obter dados do usurio do Supabase se disponvel
        let userTeam: 'green' | 'blue' | 'red' = 'green'
        let userName = 'Jogador Mock'
        let userId = 'mock-player'
        
        if (supabase) {
          try {
            const { data: { user } } = await supabase.auth.getUser()
            if (user) {
              userId = user.id
              
              // Buscar dados do usurio na tabela users
              const { data: userData, error: userError } = await supabase
                .from('users')
                .select('team, name')
                .eq('id', user.id)
                .single()
              
              if (userError) {
                console.log('gameStore: Erro ao buscar dados do usurio:', userError)
                // Se no conseguir buscar, usar dados do user_metadata
                userTeam = normalizeTeam(user.user_metadata?.team)
                userName = user.user_metadata?.name || 'Jogador'
              } else if (userData) {
                userTeam = normalizeTeam(userData.team)
                userName = userData.name || 'Jogador'
              } else {
                // Fallback para user_metadata
                userTeam = normalizeTeam(user.user_metadata?.team)
                userName = user.user_metadata?.name || 'Jogador'
              }
            }
          } catch (error) {
            console.log('gameStore: Erro ao buscar dados do usurio:', error)
          }
        }
        
        set(() => ({
          currentPlayer: {
            id: userId,
            name: userName,
            team: userTeam,
            position: position,
            score: 0
          }
        }))
      } else {
        // Atualizar apenas a posio, manter a equipa original
        set(state => ({
          currentPlayer: state.currentPlayer ? { ...state.currentPlayer, position } : null,
          players: state.players.map(player => 
            player.id === state.currentPlayer?.id 
              ? { ...player, position }
              : player
          )
        }))
      }
      
      return
    }

    // Verificar se o Supabase est configurado
    if (!supabase) {
      return
    }

    try {
      const { data: { user } } = await supabase.auth.getUser()
      if (!user) return
      
      // Converter array para formato PostgreSQL point: (lat,lng)
      const positionString = `(${position[0]},${position[1]})`
      
      const { error } = await supabase
        .from('users')
        .update({ current_position: positionString })
        .eq('id', user.id)

      if (error) {
        console.error('gameStore: Erro ao atualizar posio:', error)
        return
      }
    } catch (error) {
      console.error('Erro ao atualizar posio do usurio:', error)
    }
  },

  captureTerritory: async (territoryId: string) => {
    // Implementao omitida
    console.log('gameStore: Capturando territrio:', territoryId)
    // ...
    console.log('gameStore: Territrio capturado com sucesso')
  },

  resetGame: () => {
    console.log('gameStore: Resetando jogo...')
    set({ territories: [], players: [], currentPlayer: null })
  }
}))


