// Centro padro do mapa (Gaia - Rua da Cortinha)
const defaultCenter: [number, number] = [41.1333, -8.6167]

function makeLabeledIcon(color: string, playerName?: string) {
  const html = `
  <div class="pawn-wrap" style="--c:${color}">
    <div class="pawn-dot"></div>
    ${playerName ? `<div class="pawn-name">${playerName}</div>` : ''}
  </div>`
  return L.divIcon({
    className: '',
    html,
    iconSize: [30, 42],
    iconAnchor: [15, 21], // Centralizar o cone no ponto exato
  })
}

// Criar cone do jogador com nome
const getPlayerIcon = (user: any, onlineUsers: any[]) => {
  // Buscar dados do usurio na tabela users
  const userData = onlineUsers.find(u => u.id === user?.id)
  
  const teamColor = userData?.team === 'blue' ? '#2563eb' : 
                   userData?.team === 'red' ? '#dc2626' : '#16a34a'
  const playerName = userData?.name || user?.user_metadata?.name || 'Jogador'
  return makeLabeledIcon(teamColor, playerName)
}

function CenterOnPawnButton() {
  const map = useMap()
  const { position } = useGeo()
  
  const handleCenter = () => {
    if (position) {
      map.setView([position[0], position[1]], map.getZoom())
    } else {
      map.setView(defaultCenter, map.getZoom())
    }
  }
  
     
  
  return (
     <>
       {/* Boto centrar no peo - canto inferior esquerdo */}
       <div style={{ position: 'absolute', bottom: '10px', left: '10px', zIndex: 1000 }}>
         <button 
           className="center-button" 
           onClick={handleCenter}
           style={{ 
             backgroundColor: '#2563EB', 
             color: 'white', 
             border: 'none', 
             padding: '8px 12px', 
             borderRadius: '4px',
             fontSize: '12px',
             cursor: 'pointer',
             fontWeight: 'bold'
           }}
         >
            Centrar no peo
    </button>
       </div>
     </>
   )
}





// Funo para calcular rea de um polgono em km
function calculatePolygonArea(points: [number, number][]): number {
  if (points.length < 3) return 0
  
  let area = 0
  for (let i = 0; i < points.length; i++) {
    const j = (i + 1) % points.length
    area += points[i][0] * points[j][1]
    area -= points[j][0] * points[i][1]
  }
  
  // Converter de graus para km
  // Aproximadamente: 1 grau de latitude  111 km
  // 1 grau de longitude  111 * cos(latitude) km
  // Para simplificar, usamos uma mdia de 111 km por grau
  const areaInDegrees = Math.abs(area) / 2
  const areaInKm2 = areaInDegrees * 111 * 111
  
  return areaInKm2
}





// Funo para verificar se um ponto est dentro de um polgono (algoritmo ray casting)
function isPointInPolygon(point: [number, number], polygon: [number, number][]): boolean {
  const [x, y] = point
  let inside = false
  
  for (let i = 0, j = polygon.length - 1; i < polygon.length; j = i++) {
    const [xi, yi] = polygon[i]
    const [xj, yj] = polygon[j]
    
    if (((yi > y) !== (yj > y)) && (x < (xj - xi) * (y - yi) / (yj - yi) + xi)) {
      inside = !inside
    }
  }
  
  return inside
}







// Funo para verificar se duas reas se sobrepem
function doAreasOverlap(area1: [number, number][], area2: [number, number][]): boolean {
  // Verificar se algum ponto da rea1 est dentro da rea2
  for (const point of area1) {
    if (isPointInPolygon(point, area2)) {
      return true
    }
  }
  
  // Verificar se algum ponto da rea2 est dentro da rea1
  for (const point of area2) {
    if (isPointInPolygon(point, area1)) {
      return true
    }
  }
  
  return false
}

// Funo para calcular a opacidade baseada no nmero de sobreposies
function calculateOpacityForArea(areaIndex: number, areas: [number, number][][]): number {
  let overlapCount = 0
  
  // Contar quantas outras reas se sobrepem com esta rea
  for (let i = 0; i < areas.length; i++) {
    if (i !== areaIndex && doAreasOverlap(areas[areaIndex], areas[i])) {
      overlapCount++
    }
  }
  
  // Se no h sobreposio, usar opacidade normal
  if (overlapCount === 0) {
    return 0.3
  }
  
  // Se h sobreposio, reduzir a opacidade para compensar
  // Quanto mais sobreposies, menor a opacidade
  const baseOpacity = 0.3
  const reductionPerOverlap = 0.1
  const finalOpacity = Math.max(0.1, baseOpacity - (overlapCount * reductionPerOverlap))
  
  return finalOpacity
}





// Controles de teclado removidos (debug)

// Componente para gerenciar o trao
function TraceManager() {
  const { position } = useGeo()
  const { currentPlayer } = useGameStore()
  const { user } = useAuth()
  const { addTerritory } = useRealtime()
  const [isTracing, setIsTracing] = useState(false)
  const [trace, setTrace] = useState<[number, number][]>([])
  const [paths, setPaths] = useState<[number, number][][]>([])
  const [areas, setAreas] = useState<[number, number][][]>([])
  const lastPosition = useRef<[number, number] | null>(null)

  // Usar apenas a posio do useGeo para evitar conflitos
  const basePosition = position
  
  // Para o trao, usar sempre a posio real (sem ajuste de equipe)
  const currentPosition = useMemo(() => {
    if (!basePosition) return null
    
    // Retornar posio real sem ajustes
    return basePosition
  }, [basePosition])

  

  // Adicionar posio ao trao quando estiver rastreando
  useEffect(() => {
    if (!isTracing || !currentPosition) return

    // Verificar se a posio mudou significativamente (pelo menos 1 metro)
    if (lastPosition.current) {
      const [lastLat, lastLng] = lastPosition.current
      const [currentLat, currentLng] = currentPosition
      
      const latDiff = Math.abs(currentLat - lastLat)
      const lngDiff = Math.abs(currentLng - lastLng)
      
      // Aproximadamente 1 metro (0.00001 graus) - mais sensvel para teclado
      if (latDiff < 0.00001 && lngDiff < 0.00001) {
        return // Posio no mudou o suficiente
      }
    }

    // Adicionar posio ao trao
    const newTrace = [...trace, currentPosition]
    setTrace(newTrace)
    lastPosition.current = currentPosition
    
    console.log('TraceManager: Adicionando ponto ao trao. Total:', newTrace.length)

    // Verificar se o trao se cruza consigo mesmo (simplificado)
    if (newTrace.length >= 4) {
      const lastPoint = newTrace[newTrace.length - 1]
      
      // Verificar se o ltimo ponto est prximo de algum ponto anterior
      for (let i = 0; i < newTrace.length - 3; i++) {
        const distance = Math.sqrt(
          Math.pow(lastPoint[0] - newTrace[i][0], 2) + 
          Math.pow(lastPoint[1] - newTrace[i][1], 2)
        )
        
        // Se est a menos de 3 metros de um ponto anterior, fechar rea
        if (distance < 0.00003) {
          
          
          // Construir rea fechada
          const areaPoints = [...newTrace.slice(i), lastPoint]
          
          if (areaPoints.length >= 3) {
            const areaKm2 = calculatePolygonArea(areaPoints)
            
            // Salvar rea no Supabase (assncrono)
            if (user && addTerritory && currentPlayer?.team) {
              
              // Converter para formato GeoJSON para o Supabase
              const polygon = {
                type: 'Polygon',
                coordinates: [areaPoints.map(point => [point[1], point[0]])] // GeoJSON usa [lng, lat]
              }
              
              addTerritory(polygon, areaKm2, currentPlayer.team).catch(error => {
                console.error('TraceManager: Erro ao salvar rea:', error)
              })
            }
            
            setAreas(prev => {
              const newAreas = [...prev, areaPoints]
              const totalAreaKm2 = newAreas.reduce((sum, a) => sum + calculatePolygonArea(a), 0)
              window.dispatchEvent(new CustomEvent('areas-updated', {
                detail: { totalAreaKm2 }
              }))
              return newAreas
            })
            
            // Continuar traando a partir do ltimo ponto
            setTrace([lastPoint])
            lastPosition.current = lastPoint
            return
          }
        }
      }
    }
  }, [currentPosition, isTracing, trace, user, addTerritory, currentPlayer?.team])

  // Funo para iniciar trao
  const startTrace = () => {
    console.log('TraceManager: Iniciando trao...')
    setIsTracing(true)
    setTrace([])
    lastPosition.current = currentPosition
    // Emitir evento para sincronizar com Controls
    window.dispatchEvent(new CustomEvent('trace-state-updated', {
      detail: { isTracing: true }
    }))
    console.log('TraceManager: Trao iniciado com sucesso')
  }

  // Funo para parar trao
  const stopTrace = () => {
    
    setIsTracing(false)
    if (trace.length > 1) {
      setPaths(prev => [...prev, trace])
    }
    setTrace([])
    // Emitir evento para sincronizar com Controls
    window.dispatchEvent(new CustomEvent('trace-state-updated', {
      detail: { isTracing: false }
    }))
  }

  // Funo para resetar todos os traos e reas
  const resetTraces = () => {
    setTrace([])
    setPaths([])
    setAreas([])
    setIsTracing(false)
    // Emitir evento para atualizar o total de rea
    window.dispatchEvent(new CustomEvent('areas-updated', { 
      detail: { totalAreaKm2: 0 } 
    }))
  }

  // Expor funes para controles de teclado e reset
  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      if (event.key === ' ' || event.key === 'Enter') {
        event.preventDefault()
        if (isTracing) {
          stopTrace()
        } else {
          startTrace()
        }
      }
    }

    const handleResetTraces = () => {
      resetTraces()
    }

    const handleForceCloseArea = () => {
      if (trace.length >= 3) {
        // Fechar o polgono adicionando o primeiro ponto no final
        const closedTrace = [...trace, trace[0]]
        const area = calculatePolygonArea(closedTrace)
        const minArea = 0.00000001
        
        if (area > minArea) {
          // Fechar rea foradamente
          setAreas(prev => {
            const newAreas = [...prev, trace]
            const totalAreaKm2 = newAreas.reduce((sum, a) => sum + calculatePolygonArea(a), 0)
            window.dispatchEvent(new CustomEvent('areas-updated', { 
              detail: { totalAreaKm2 } 
            }))
            return newAreas
          })
          // Continuar traando aps fechar rea foradamente
          setTrace([trace[trace.length - 1]]) // Comear novo trao a partir do ltimo ponto
          lastPosition.current = trace[trace.length - 1]
        }
      }
    }

    const handleStartTraceEvent = () => {
      startTrace()
    }

    const handleStopTraceEvent = () => {
      stopTrace()
    }

    window.addEventListener('keydown', handleKeyDown)
    window.addEventListener('reset-traces', handleResetTraces)
    window.addEventListener('force-close-area', handleForceCloseArea)
    window.addEventListener('start-trace', handleStartTraceEvent)
    window.addEventListener('stop-trace', handleStopTraceEvent)
    
    return () => {
      window.removeEventListener('keydown', handleKeyDown)
      window.removeEventListener('reset-traces', handleResetTraces)
      window.removeEventListener('force-close-area', handleForceCloseArea)
      window.removeEventListener('start-trace', handleStartTraceEvent)
      window.removeEventListener('stop-trace', handleStopTraceEvent)
    }
  }, [isTracing])



  return {
    isTracing,
    trace,
    paths,
    areas,
    startTrace,
    stopTrace,
    resetTraces
  }
}

// Funo para dividir um polgono quando h sobreposio
function splitPolygonOnOverlap(territory: any, overlaps: any[]) {
  const baseCoords = territory.polygon?.coordinates?.[0] || []
  
  if (baseCoords.length === 0) {
    return null
  }
  
  // Encontrar sobreposies onde este territrio perdeu
  const losingOverlaps = overlaps.filter(overlap => overlap.losingTerritory.id === territory.id)
  
  if (losingOverlaps.length === 0) {
    // Sem sobreposies, retornar polgono original
    return [{
      positions: baseCoords.map((coord: number[]) => ({
        lat: coord[1],
        lng: coord[0]
      }))
    }]
  }
  
  // Com sobreposies, dividir o polgono em sees
  const overlappingAreas = losingOverlaps.map(overlap => 
    overlap.conqueredTerritory.polygon?.coordinates?.[0] || []
  ).filter(area => area.length > 0)
  
  if (overlappingAreas.length === 0) {
    return [{
      positions: baseCoords.map((coord: number[]) => ({
        lat: coord[1],
        lng: coord[0]
      }))
    }]
  }
  
  // Converter coordenadas base para formato lat/lng
  const basePolygon = baseCoords.map((coord: number[]) => ({
    lat: coord[1],
    lng: coord[0]
  }))
  
  // Para cada sobreposio, dividir o polgono base
  let remainingPolygons = [basePolygon]
  
  overlappingAreas.forEach((overlapCoords) => {
    const overlapBounds = getPolygonBounds(overlapCoords)
    const newRemainingPolygons: Array<Array<{lat: number, lng: number}>> = []
    
    // Dividir cada polgono restante
    remainingPolygons.forEach(polygon => {
             const polygonBounds = {
         minLat: Math.min(...polygon.map((p: {lat: number, lng: number}) => p.lat)),
         maxLat: Math.max(...polygon.map((p: {lat: number, lng: number}) => p.lat)),
         minLng: Math.min(...polygon.map((p: {lat: number, lng: number}) => p.lng)),
         maxLng: Math.max(...polygon.map((p: {lat: number, lng: number}) => p.lng))
       }
      
      // Verificar se h sobreposio entre este polgono e a rea conquistada
      const hasOverlap = !(
        polygonBounds.maxLat < overlapBounds.minLat ||
        polygonBounds.minLat > overlapBounds.maxLat ||
        polygonBounds.maxLng < overlapBounds.minLng ||
        polygonBounds.minLng > overlapBounds.maxLng
      )
      
      if (!hasOverlap) {
        // Sem sobreposio, manter o polgono intacto
        newRemainingPolygons.push(polygon)
        return
      }
      
      // Com sobreposio, dividir o polgono em sees
      const sections: Array<Array<{lat: number, lng: number}>> = []
      
      // Seo superior (se houver espao acima da sobreposio)
      if (overlapBounds.maxLat < polygonBounds.maxLat) {
        sections.push([
          { lat: overlapBounds.maxLat, lng: polygonBounds.minLng },
          { lat: polygonBounds.maxLat, lng: polygonBounds.minLng },
          { lat: polygonBounds.maxLat, lng: polygonBounds.maxLng },
          { lat: overlapBounds.maxLat, lng: polygonBounds.maxLng }
        ])
      }
      
      // Seo direita (se houver espao  direita da sobreposio)
      if (overlapBounds.maxLng < polygonBounds.maxLng) {
        sections.push([
          { lat: overlapBounds.minLat, lng: overlapBounds.maxLng },
          { lat: overlapBounds.maxLat, lng: overlapBounds.maxLng },
          { lat: overlapBounds.maxLat, lng: polygonBounds.maxLng },
          { lat: overlapBounds.minLat, lng: polygonBounds.maxLng }
        ])
      }
      
      // Seo inferior (se houver espao abaixo da sobreposio)
      if (overlapBounds.minLat > polygonBounds.minLat) {
        sections.push([
          { lat: polygonBounds.minLat, lng: polygonBounds.minLng },
          { lat: overlapBounds.minLat, lng: polygonBounds.minLng },
          { lat: overlapBounds.minLat, lng: polygonBounds.maxLng },
          { lat: polygonBounds.minLat, lng: polygonBounds.maxLng }
        ])
      }
      
      // Seo esquerda (se houver espao  esquerda da sobreposio)
      if (overlapBounds.minLng > polygonBounds.minLng) {
        sections.push([
          { lat: overlapBounds.minLat, lng: polygonBounds.minLng },
          { lat: overlapBounds.maxLat, lng: polygonBounds.minLng },
          { lat: overlapBounds.maxLat, lng: overlapBounds.minLng },
          { lat: overlapBounds.minLat, lng: overlapBounds.minLng }
        ])
      }
      
      // Adicionar sees vlidas (com pelo menos 3 pontos)
      sections.forEach(section => {
        if (section.length >= 3) {
          newRemainingPolygons.push(section)
        }
      })
    })
    
    remainingPolygons = newRemainingPolygons
  })
  
  // Se no conseguiu dividir, retornar polgono original
  if (remainingPolygons.length === 0) {
    return [{
      positions: baseCoords.map((coord: number[]) => ({
        lat: coord[1],
        lng: coord[0]
      }))
    }]
  }
  
  // Converter de volta para o formato esperado
  return remainingPolygons.map(polygon => ({
    positions: polygon
  }))
}

// Funo para calcular os limites de um polgono
function getPolygonBounds(coords: number[][]) {
  const lats = coords.map(p => p[1])
  const lngs = coords.map(p => p[0])
  
  return {
    minLat: Math.min(...lats),
    maxLat: Math.max(...lats),
    minLng: Math.min(...lngs),
    maxLng: Math.max(...lngs)
  }
}

// Funo para detectar sobreposies entre territrios
function detectTerritoryOverlaps(territories: any[]) {
  
  const overlaps: Array<{
    conqueredTerritory: any
    losingTerritory: any
    overlapArea: number
  }> = []

  for (let i = 0; i < territories.length; i++) {
    for (let j = i + 1; j < territories.length; j++) {
      const t1 = territories[i]
      const t2 = territories[j]
      
      // S verificar entre equipes diferentes
      if (t1.team_id === t2.team_id) continue
      
      // Verificar se h sobreposio temporal e geogrfica
      const t1Date = new Date(t1.conquered_at).getTime()
      const t2Date = new Date(t2.conquered_at).getTime()
      
      // Determinar qual foi conquistado depois (conquistador)
      const conqueringTerritory = t1Date > t2Date ? t1 : t2
      const losingTerritory = t1Date > t2Date ? t2 : t1
      
      // Verificar se h sobreposio real baseada nos limites dos polgonos
      const coords1 = t1.polygon?.coordinates?.[0] || []
      const coords2 = t2.polygon?.coordinates?.[0] || []
      
      if (coords1.length === 0 || coords2.length === 0) continue
      
      const bounds1 = getPolygonBounds(coords1)
      const bounds2 = getPolygonBounds(coords2)
      
      // Verificar se os retngulos delimitadores se sobrepem
      const hasOverlap = !(
        bounds1.maxLat < bounds2.minLat ||
        bounds1.minLat > bounds2.maxLat ||
        bounds1.maxLng < bounds2.minLng ||
        bounds1.minLng > bounds2.maxLng
      )
      
      if (hasOverlap) {
        // Calcular rea de sobreposio estimada
        const overlapWidth = Math.min(bounds1.maxLng, bounds2.maxLng) - Math.max(bounds1.minLng, bounds2.minLng)
        const overlapHeight = Math.min(bounds1.maxLat, bounds2.maxLat) - Math.max(bounds1.minLat, bounds2.minLat)
        const overlapArea = Math.max(0, overlapWidth * overlapHeight * 111 * 111) // Converter para km
        
        overlaps.push({
          conqueredTerritory: conqueringTerritory,
          losingTerritory: losingTerritory,
          overlapArea
        })
      }
    }
  }
  
  return overlaps
}



export function MapView() {
  const { user } = useAuth()
  const { position, updatePosition } = useGeo()
  const { onlineUsers, territories } = useRealtime()
  const { currentPlayer } = useGameStore()
  
  // Criar TraceManager apenas uma vez para evitar recriaes
  const traceManager = useMemo(() => {
    console.log('MapView: Criando TraceManager')
    return TraceManager()
  }, [])

  // Controles de teclado para movimento
  const handleMove = (direction: 'up' | 'down' | 'left' | 'right') => {
    if (!position) return
    
    const step = 0.0001 // Passo de movimento (aproximadamente 10 metros)
    let newPosition: [number, number] = [...position]
    
    switch (direction) {
      case 'up':
        newPosition = [position[0] + step, position[1]]
        break
      case 'down':
        newPosition = [position[0] - step, position[1]]
        break
      case 'left':
        newPosition = [position[0], position[1] - step]
        break
      case 'right':
        newPosition = [position[0], position[1] + step]
        break
    }
    
    // Iniciar trao automaticamente na primeira movimentao
    if (!traceManager.isTracing) {
      console.log('MapView: Iniciando trao automaticamente na primeira movimentao')
      traceManager.startTrace()
    }
    
    updatePosition(newPosition)
  }

  // Ativar controles de teclado
  const { isMoving } = useKeyboardControls({
    onMove: handleMove,
    enabled: true
  })

  // Usar apenas a posio do useGeo para evitar conflitos
  const currentPosition = position || defaultCenter
  
  // Ajustar posio do marker para jogadores verdes (1 km  esquerda)
  const adjustedPosition = useMemo(() => {
    const basePosition = currentPosition
    const myTeam = currentPlayer?.team || onlineUsers.find(u => u.id === user?.id)?.team || 'green'
    
    // Se for equipe verde, mover 1 km para a esquerda
    if (myTeam === 'green') {
      // 1 km  0.009 graus de longitude (aproximadamente)
      const offsetLng = -0.009
      return [basePosition[0], basePosition[1] + offsetLng]
    }
    
    return basePosition
  }, [currentPosition, currentPlayer?.team, onlineUsers, user?.id])
  
  const positionMemo = useMemo(() => ({ lat: adjustedPosition[0], lng: adjustedPosition[1] }), [adjustedPosition])
  
  // Determinar cor da equipa do jogador atual (fallback por onlineUsers)
  const myTeam = useMemo(() => {
    const norm = (t?: string) => {
      const v = (t || '').toString().toLowerCase().trim()
      if (v === 'red') return 'red'
      if (v === 'blue') return 'blue'
      if (v === 'green') return 'green'
      console.warn('MapView normalizeTeam: Equipe invlida:', t, 'usando fallback green')
      return 'green'
    }
    if (currentPlayer?.team) return norm(currentPlayer.team)
    const me = onlineUsers.find(u => u.id === user?.id)
    return norm(me?.team)
  }, [currentPlayer?.team, onlineUsers, user?.id])
  
  const myTeamColor = myTeam === 'blue' ? '#2563eb' : myTeam === 'red' ? '#dc2626' : '#16a34a'

  const [mapLoaded, setMapLoaded] = useState(false)
  const [mapError, setMapError] = useState<string | null>(null)
  const [isIPad, setIsIPad] = useState(false)

  // Detectar iPad
  useEffect(() => {
    const userAgent = navigator.userAgent.toLowerCase()
    const isIPadDevice = /ipad/.test(userAgent) || 
                        (navigator.platform === 'MacIntel' && navigator.maxTouchPoints > 1)
    setIsIPad(isIPadDevice)
  }, [])

  // Detectar sobreposies
  const territoryOverlaps = useMemo(() => {
    return detectTerritoryOverlaps(territories)
  }, [territories])

  // Remover lgica de forar atualizao para evitar conflitos


  // Timeout para forar carregamento do mapa (fallback para iPad)
  useEffect(() => {
    const timeout = setTimeout(() => {
      if (!mapLoaded) {
        setMapLoaded(true)
      }
    }, isIPad ? 5000 : 3000) // Mais tempo para iPad

    return () => clearTimeout(timeout)
  }, [mapLoaded, isIPad])

  // Handler para quando o mapa carrega
  const handleMapLoad = () => {
    setMapLoaded(true)
    setMapError(null)
  }

  // Handler para erros do mapa
  const handleMapError = (error?: any) => {
    console.error('MapView: Erro no carregamento do mapa:', error)
    setMapError(error?.message || 'Erro desconhecido')
    // Forar carregamento mesmo com erro aps um delay
    setTimeout(() => setMapLoaded(true), 2000)
  }

  // Se o mapa ainda no carregou, mostrar loading
  if (!mapLoaded) {
    return (
      <div style={{ height: '100%', width: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center', backgroundColor: '#1f2937' }}>
        <div style={{ color: 'white', textAlign: 'center' }}>
          <div style={{ fontSize: '24px', marginBottom: '10px' }}></div>
          <div>Carregando mapa...</div>
          <div style={{ fontSize: '12px', marginTop: '10px', opacity: 0.7 }}>
            {isIPad ? 'iPad detectado - Carregamento otimizado' : 'Verificando GPS e conectividade'}
          </div>
          <div style={{ fontSize: '10px', marginTop: '5px', opacity: 0.5 }}>
            {navigator.userAgent.includes('Safari') ? 'Safari' : 
             navigator.userAgent.includes('Chrome') ? 'Chrome' : 'Navegador'} no {isIPad ? 'iPad' : 'dispositivo mvel'}
          </div>
          {mapError && (
            <div style={{ fontSize: '10px', marginTop: '5px', color: '#ef4444' }}>
              Erro: {mapError}
            </div>
          )}
          <button 
            onClick={() => setMapLoaded(true)}
            style={{
              marginTop: '10px',
              padding: '8px 16px',
              backgroundColor: '#2563EB',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            Forar Carregamento
          </button>
        </div>
      </div>
    )
  }

  // Fallback se Leaflet no estiver disponvel
  if (typeof L === 'undefined') {
    return (
      <div style={{ height: '100%', width: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center', backgroundColor: '#1f2937' }}>
        <div style={{ color: 'white', textAlign: 'center' }}>
          <div style={{ fontSize: '24px', marginBottom: '10px' }}></div>
          <div>Erro: Leaflet no carregado</div>
          <div style={{ fontSize: '12px', marginTop: '10px', opacity: '0.7' }}>
            Tente recarregar a pgina
          </div>
        </div>
      </div>
    )
  }

  return (
    <div style={{ position: 'relative', height: '100%', width: '100%' }}>
      <MapContainer 
        center={defaultCenter} 
        zoom={15} 
        style={{ height: '100%', width: '100%' }}
        whenReady={handleMapLoad}
      >
        <TileLayer
          attribution="&copy; <a href='https://www.openstreetmap.org/copyright'>OpenStreetMap</a> contributors"
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          eventHandlers={{
            error: handleMapError
          }}
        />
        
        {/* Peo do jogador */}
        <Marker position={positionMemo} icon={getPlayerIcon(user, onlineUsers)} />

      {/* Outros jogadores - REMOVIDO POR SEGURANA */}
      {/* Cada jogador s v o seu prprio marker */}
    
      {/* Linha de trao atual */}
      {traceManager.trace.length > 0 && (
        <>
          {console.log('MapView: Renderizando trao com', traceManager.trace.length, 'pontos')}
          <Polyline 
            positions={traceManager.trace.map(([lat, lng]) => ({ lat, lng }))} 
            color={myTeamColor} 
            pathOptions={{ weight: 6, zIndex: 1000 }} 
          />
        </>
      )}
      
      {/* Paths persistentes */}
      {traceManager.paths.map((path, i) => (
        <Polyline 
          key={`path-${i}`} 
          positions={path.map(([lat, lng]) => ({ lat, lng }))} 
          color={myTeamColor} 
          pathOptions={{ weight: 8 }} 
        />
      ))}
      
      {/* reas fechadas com opacidade ajustada */}
      {traceManager.areas.map((area, i) => {
        const opacity = calculateOpacityForArea(i, traceManager.areas)
        return (
          <Polygon 
            key={`area-local-${i}`} 
            positions={area.map(([lat, lng]) => ({ lat, lng }))} 
            pathOptions={{ 
              color: myTeamColor, 
              fillColor: myTeamColor, 
              fillOpacity: opacity,
              weight: 3 
            }} 
          />
        )
      })}
      
      {/* reas vindas do Supabase (todos os jogadores) */}
      {territories.map((territory) => {
        // Dividir polgono se h sobreposies
        const splitPolygons = splitPolygonOnOverlap(territory, territoryOverlaps)
        
        if (!splitPolygons) {
          return null
        }
        
        // Determinar cor baseada na equipa (normalizada)
        const teamKey = (territory.team_id || 'green').toString().toLowerCase()
        const teamColor = teamKey === 'blue' ? '#2563eb' : 
                         teamKey === 'red' ? '#dc2626' : '#16a34a'
        
        return (
          <div key={`territory-${territory.id}`}>
            {/* Renderizar cada parte do polgono dividido */}
            {splitPolygons.map((polygon, index) => {
              // Usar a cor original da equipe para todas as sees
              return (
                <Polygon 
                  key={`split-${territory.id}-${index}`}
                  positions={polygon.positions}
                  pathOptions={{ 
                    color: teamColor, 
                    fillColor: teamColor, 
                    fillOpacity: 0.3,
                    weight: 3 
                  }} 
                />
              )
            })}
          </div>
        )
      })}



      <CenterOnPawnButton />
    </MapContainer>
    
    {/* Instrues de controle e botes de debug */}
    <div style={{ position: 'absolute', bottom: '10px', left: '10px', zIndex: 1000 }}>
      {/* Instrues de controle */}
      <div style={{ 
        background: 'rgba(0,0,0,0.8)', 
        color: 'white', 
        padding: '10px', 
        borderRadius: '5px',
        fontSize: '12px',
        marginBottom: '10px',
        maxWidth: '200px'
      }}>
        <div style={{ fontWeight: 'bold', marginBottom: '5px' }}> Controles:</div>
        <div>WASD ou Setas</div>
        <div>Mover o marker</div>
        {isMoving && <div style={{ color: '#10b981', marginTop: '5px' }}> Movendo...</div>}
      </div>
      
      {/* Boto de teste */}
      <button 
        onClick={() => {
          console.log('Teste: Movendo marker...')
          const newPos: [number, number] = [41.1333 + (Math.random() - 0.5) * 0.01, -8.6167 + (Math.random() - 0.5) * 0.01]
          console.log('Teste: Nova posio:', newPos)
          
          // Atualizar diretamente o useGeo
          updatePosition(newPos)
          
          alert(`Marker movido para: ${newPos[0].toFixed(6)}, ${newPos[1].toFixed(6)}`)
        }}
        style={{ 
          background: 'red', 
          color: 'white', 
          border: 'none', 
          padding: '10px', 
          borderRadius: '5px',
          cursor: 'pointer',
          fontSize: '12px',
          fontWeight: 'bold',
          boxShadow: '0 2px 4px rgba(0,0,0,0.3)',
          marginBottom: '5px',
          width: '100%'
        }}
      >
         Teste: Mover Marker
      </button>
      
      {/* Boto de teste do trao */}
      <button 
        onClick={() => {
          console.log('=== TESTE DO TRAO ===')
          console.log('TraceManager: isTracing:', traceManager.isTracing)
          console.log('TraceManager: trace.length:', traceManager.trace.length)
          console.log('TraceManager: currentPosition:', position)
          console.log('TraceManager: currentPlayer:', currentPlayer)
          
          if (traceManager.isTracing) {
            traceManager.stopTrace()
            alert('Trao parado!')
          } else {
            traceManager.startTrace()
            alert('Trao iniciado! Agora mova o marker com WASD.')
          }
        }}
        style={{ 
          background: '#16a34a', 
          color: 'white', 
          border: 'none', 
          padding: '8px', 
          borderRadius: '5px',
          cursor: 'pointer',
          fontSize: '10px',
          width: '100%',
          marginBottom: '5px'
        }}
      >
        {traceManager.isTracing ? ' Parar Trao' : ' Iniciar Trao'}
      </button>
      
      {/* Boto de debug para equipe vermelha */}
      <button 
        onClick={() => {
          console.log('=== DEBUG EQUIPE VERMELHA ===')
          console.log('MapView: myTeam:', myTeam)
          console.log('MapView: myTeamColor:', myTeamColor)
          console.log('MapView: currentPlayer:', currentPlayer)
          console.log('MapView: traceManager.trace.length:', traceManager.trace.length)
          console.log('MapView: traceManager.trace:', traceManager.trace)
          console.log('MapView: position:', position)
          console.log('MapView: positionMemo:', positionMemo)
          alert(`Equipe: ${myTeam}\nCor: ${myTeamColor}\nTrao: ${traceManager.trace.length} pontos`)
        }}
        style={{ 
          background: '#dc2626', 
          color: 'white', 
          border: 'none', 
          padding: '8px', 
          borderRadius: '5px',
          cursor: 'pointer',
          fontSize: '10px',
          width: '100%'
        }}
      >
         Debug Vermelho
      </button>
    </div>
  </div>
  )
}


