import { useEffect } from 'react'
import { useGameStore } from '@/store/gameStore'
import { useGeo } from './useGeo'

export function useTrace() {
  const { position } = useGeo()
  
  useEffect(() => {
    // Remover verificao de modo mock para permitir funcionamento

    const interval = setInterval(() => {
      if (position) {
        // Atualizar posio do jogador no gameStore
        useGameStore.getState().updatePlayerPosition(position)
      }
    }, 500) // Reduzir para 500ms para atualizao mais frequente

    return () => {
      clearInterval(interval)
    }
  }, [position])
}
